html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-inter);
  background: var(--background);
  color: var(--text-color);
}

.visually-hidden {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

h1, h2, h3 {
  margin-top: 0;
  line-height: 1.1;
}

a, a:visited, a:active {
  text-decoration: none;
}

h1 {
  font-size: 1.5rem;
  margin-bottom: 0.1875rem;
}
@media (min-width: 71.8125em) {
  h1 {
    font-size: 1.75rem;
  }
}

h2 {
  font-size: 1.5rem;
  margin-bottom: 1.5rem;
}

.attribution {
  font-size: 11px;
  text-align: center;
}

.attribution a {
  color: #3e52a3;
}

:root {
  --font-inter: "Inter", sans-serif;
}

:root {
  --limegreen: hsl(163, 72%, 41%);
  --brightred: hsl(356, 69%, 56%);
  --facebook: hsl(208, 92%, 53%);
  --twitter: hsl(203, 89%, 53%);
  --instagram-start: hsl(37, 97%, 70%) ;
  --instagram-middle: hsl(5, 77%, 71%);
  --instagram-end: hsl(329, 70%, 58%);
  --youtube: hsl(348, 97%, 39%);
  --toggle-bg-light: hsl(230, 22%, 74%);
  --toggle-bg-start: hsl(210, 78%, 56%);
  --toggle-bg-end:hsl(146, 68%, 55%);
  --toggle-light: hsl(230, 19%, 60%);
  --toggle-button-light: hsl(228, 46%, 96%);
  --dark-bg: hsl(230, 17%, 14%);
  --dark-top-bg: hsl(232, 19%, 15%);
  --dark-card: hsl(228, 28%, 20%);
  --dark-card-hover: hsl(228, 25%, 27%);
  --dark-text1: hsl(228, 34%, 66%);
  --dark-text2: hsl(0, 0%, 100%);
  --light-bg: hsl(0, 0%, 100%);
  --light-top-bg: hsl(225, 100%, 98%);
  --light-card: hsl(227, 47%, 96%);
  --light-card-hover: hsl(228, 33, 91);
  --light-text1: hsl(230, 12%, 44%);
  --light-text2: hsl(230, 17%, 14%);
  --spotify: hsl(141, 76%, 48%);
  --reddit: hsl(16, 100%, 50%);
  --tiktok:hsl(178, 100%, 47%);
  --background: var(--light-bg);
  --text-color: var(--light-text1);
  --text-color2: var(--light-text2);
  --card-bg: var(--light-card);
  --card-hover: var(--light-card-hover);
  --top-bg: var(--light-top-bg);
  --toggle: var(--toggle-light);
  --toggle-bg: var(--toggle-bg-light);
  --toggle-button: var(--toggle-button-light);
}

@media (prefers-color-scheme: dark) {
  :root {
    --background: var(--dark-bg);
    --text-color: var(--dark-text2);
    --text-color2: var(--dark-text2);
    --card-bg: var(--dark-card);
    --card-hover: var(--dark-card-hover);
    --top-bg: var(--dark-top-bg);
    --toggle: var(--light-bg);
    --toggle-bg: linear-gradient(225deg, var(--toggle-bg-end)0%, var(--toggle-bg-start)98.02%);
    --toggle-button: var(--dark-bg);
  }
}
body.light {
  --background: var(--light-bg);
  --text-color: var(--light-text1);
  --text-color2: var(--light-text2);
  --card-bg: var(--light-card);
  --card-hover: var(--light-card-hover);
  --top-bg: var(--light-top-bg);
  --toggle: var(--toggle-light);
  --toggle-bg: var(--toggle-bg-light);
  --toggle-button: var(--toggle-button-light);
}

body.dark {
  --background: var(--dark-bg);
  --text-color: var(--dark-text2);
  --text-color2: var(--dark-text2);
  --card-bg: var(--dark-card);
  --card-hover: var(--dark-card-hover);
  --top-bg: var(--dark-top-bg);
  --toggle: var(--light-bg);
  --toggle-bg: linear-gradient(225deg, var(--toggle-bg-end)0%, var(--toggle-bg-start)98.02%);
  --toggle-button: var(--dark-bg);
}

.container {
  max-width: 69.375rem;
  padding: 0 1.5625rem;
  margin: 0 auto 2.875rem;
}
@media (min-width: 71.8125em) {
  .container {
    padding: 0;
  }
}

.card-grid {
  display: grid;
  gap: 1.875rem;
  grid-template-columns: 1fr;
}
@media (min-width: 40em) {
  .card-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 71.8125em) {
  .card-grid {
    grid-template-columns: repeat(4, 1fr);
  }
}

.header {
  margin-top: 2.25rem;
  display: flex;
  flex-wrap: wrap;
}
@media (min-width: 40em) {
  .header {
    justify-content: space-between;
    align-items: center;
  }
}
.header__title {
  width: 100%;
}
@media (min-width: 40em) {
  .header__title {
    width: auto;
  }
}
.header__subtitle {
  font-size: 0.875rem;
  font-weight: 700;
  color: var(text-color2);
}

.toggle {
  display: grid;
  grid-template-columns: 1fr 3rem;
  border: none;
  margin: 0;
}
.toggle label {
  font-size: 0.875rem;
  font-weight: 700;
  color: var(--toggle);
}
.toggle label[for=dark] {
  line-height: 1.5rem;
  margin-right: 0.8125rem;
}
.toggle__wrapper {
  height: 1.5rem;
  position: relative;
}
.toggle input[type=radio] {
  margin: 0 -0.125rem 0 -0.125rem;
  opacity: 0;
  width: 1.5rem;
  height: 1.5rem;
}
.toggle input[type=radio]:focus ~ .toggle__button {
  border: 2px solid white;
}
.toggle__background {
  background: var(--toggle-bg);
  display: block;
  height: 100%;
  border-radius: 0.75rem;
  width: 100%;
  top: 0;
  position: absolute;
  pointer-events: none;
}
.toggle__button {
  position: absolute;
  left: 0.1875rem;
  top: 0.1875rem;
  right: 100%;
  width: 1.125rem;
  height: 1.125rem;
  border-radius: 50%;
  background-color: var(--toggle-button);
  transition: all 150ms ease-in-out;
}
.toggle__button ~ #dark:light {
  right: 0;
  left: 100%;
}
.toggle #light:checked ~ .toggle__button {
  left: calc(100% - 21px);
  right: 3px;
}
.toggle #system:checked ~ .toggle__button {
  left: 50%;
  right: auto;
  transform: translate(-50%);
}

.cards {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1.875rem;
}
@media (min-width: 40em) {
  .cards {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 71.8125em) {
  .cards {
    grid-template-columns: repeat(4, 1fr);
  }
}

.card {
  background: var(--card-bg);
  color: --text-color;
  text-align: center;
  padding: 1.5625rem;
  border-radius: 0.3125rem;
  position: relative;
  overflow: hidden;
  transition: background 150ms ease-in-out;
  cursor: pointer;
}
.card:hover {
  background: var(--card-hover);
  transform: scale(1.1);
}
.card--facebook {
  border-top: 0.3125rem solid var(--facebook);
}
.card--facebook:hover {
  border: 0.0625rem solid var(--facebook);
  box-shadow: 0 0 0.625rem var(--facebook);
}
.card--twitter {
  border-top: 0.3125rem solid var(--twitter);
}
.card--twitter:hover {
  border: 0.0625rem solid var(--twitter);
  box-shadow: 0 0 0.625rem var(--twitter);
}
.card--spotify {
  border-top: 0.3125rem solid var(--spotify);
}
.card--spotify:hover {
  border: 0.0625rem solid var(--spotify);
  box-shadow: 0 0 0.625rem var(--spotify);
}
.card--reddit {
  border-top: 0.3125rem solid var(--reddit);
}
.card--reddit:hover {
  border: 0.0625rem solid var(--reddit);
  box-shadow: 0 0 0.625rem var(--reddit);
}
.card--tiktok {
  border-top: 0.3125rem solid var(--tiktok);
}
.card--tiktok:hover {
  border: 0.0625rem solid var(--tiktok);
  box-shadow: 0 0 0.625rem var(--tiktok);
}
.card--instagram {
  border-top: 0.3125rem solid var(--instagram-middle);
}
.card--instagram:hover {
  border: 0.0625rem solid var(--instagram-middle);
  box-shadow: 0 0 0.625rem var(--instagram-end);
}
.card--threads {
  border-top: 0.3125rem solid var(--instagram-end);
}
.card--threads:hover {
  border: 0.0625rem solid var(--instagram-middle);
  box-shadow: 0 0 0.625rem var(--instagram-end);
}
.card--youtube {
  border-top: 0.3125rem solid var(--youtube);
}
.card--youtube:hover {
  border: 0.0625rem solid var(--youtube);
  box-shadow: 0 0 0.625rem var(--youtube);
}
.card img {
  max-width: 1.25rem;
  max-height: 1.25rem;
}
.card__platform {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 1.75rem;
  margin-top: 0.3125rem;
  height: 1.25rem;
}
.card__subtitle {
  font-size: 0.875rem;
  font-weight: 700;
}
.card__icon {
  margin-right: 0.5rem;
}
.card__username {
  font-size: re(12);
  font-weight: bold;
  color: var(--text-color);
}
.card__count {
  color: var(--text-color);
  letter-spacing: -0.125rem;
  line-height: 1;
  margin-bottom: 0.25rem;
}
.card__count--big {
  font-size: 3.5rem;
  font-weight: 700;
}
.card__count--small {
  font-size: 2rem;
  font-weight: 700;
}
.card__label {
  color: var(--toggle);
  text-transform: uppercase;
  font-size: 0.75rem;
  font-weight: 400;
  letter-spacing: 0.3125rem;
}
.card__followers {
  margin-bottom: 1.5625rem;
}
.card__change {
  font-size: 0.75rem;
  font-weight: 700;
  display: flex;
  align-items: center;
  justify-content: center;
}
.card__change--up {
  color: var(--limegreen);
}
.card__change--down {
  color: var(--brightred);
}
.card__number {
  margin-left: 0.25rem;
}

.card-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, auto);
  justify-items: start;
  gap: 1.4375rem;
}
.card-grid .card__count {
  margin-bottom: 0;
}
.card-grid img, .card-grid .card__change {
  justify-self: end;
}
.card-grid .card__count, .card-grid .card__change {
  align-self: end;
}